@include keyframes(strainFadeIn) {
  0% {
    opacity: 0;
    @include transform(translateY(55px));
  }
  100% {
    opacity: 1;
    @include transform(translateY(0px));
  }
}

@mixin strainFadeIn($count: $countDefault, $duration: $durationDefault, $delay: $delayDefault, $function: $functionDefault, $fill: $fillDefault, $visibility: $visibilityDefault) {
  @include animation-name(strainFadeIn);
  @include count($count);
  @include duration($duration);
  @include delay($delay);
  @include function($function);
  @include fill-mode($fill);
  @include visibility($visibility);
}